.saver, .threedsaver {
  z-index: 1000;
  width: 150px;
  padding: 20px;
  font-size: 30px;
  position: fixed;
  bottom: 10px;
  left: 10px;
}

.saver {
  color: #fff;
  background: none;
  border: solid;
  font-weight: 400;
}

.threedsaver {
  color: #000;
  background: #fff;
  border-radius: 50%;
  font-weight: 600;
  box-shadow: 0 0 0 1.3pt #000;
}

.saver:hover, .saver:focus {
  color: #fff;
  background: #000;
  font-weight: 400;
}

.threedsaver:hover, .threedsaver:focus {
  color: #000;
  font-weight: 900;
  box-shadow: 0 0 0 4pt #000;
}

.grabcursor {
  cursor: grab;
}

.tooltip {
  display: inline-block;
  position: relative;
}

.tooltip .tooltiptext {
  visibility: hidden;
  color: #000;
  text-align: center;
  z-index: 1;
  opacity: 0;
  background-color: #fff;
  border: 5px solid #000;
  border-radius: 45%;
  flex-direction: column;
  align-items: center;
  min-width: 120px;
  margin-left: -60px;
  padding: 5px 0;
  transition: opacity .3s;
  display: flex;
  position: absolute;
  bottom: 150%;
  left: 50%;
}

.tooltip .tooltiptext:before {
  content: "";
  border: 20px solid #0000;
  border-top-color: #000;
  margin-left: -20px;
  position: absolute;
  top: 100%;
  left: 50%;
}

.tooltip .tooltiptext:after {
  content: "";
  border: 12px solid #0000;
  border-top-color: #fff;
  margin-left: -12px;
  position: absolute;
  top: 100%;
  left: 50%;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

