  display: block;
}

.noShadow:after {
  content: none;
}

.slideContainer {
  width: 70%;
  position: fixed;
  bottom: 10px;
  left: 60%;
  transform: translateX(-50%);
}

.slider {
  -webkit-appearance: none;
  -webkit-transition: all .2s;
  background: #fff;
  border-radius: 10px;
  outline: none;
  width: 100%;
  height: 25px;
  transition: opacity .2s;
}

.slider:hover {
  box-shadow: 0 0 0 3px #000;
}

.slider::-webkit-slider-thumb {
  appearance: none;
  cursor: pointer;
  background: center / contain;
  width: 25px;
  height: 25px;
}

.slider::-moz-range-thumb {
  cursor: pointer;
  background: center / contain;
  border: none;
  width: 25px;
  height: 25px;
}

